* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

:root {
	--primary: #F8F8F8;
	--secondary: steelblue;
	--text-color: #555;
	--text-light: #888;
	--component-shadow: inset 0px 1px 3px rgba(0,0,0,0.05);
}

body {
	background: #F8F8F8;
	background: var(--primary);
	color: #555;
	color: var(--text-color); 
	font-family: 'segoe UI';
	letter-spacing: 1px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.container {
	width: 400px;
	min-height: 500px;
	position: relative;
	text-align: center;
	margin: 10vh auto;
	padding: 0.5rem;
	border-radius: 5px;
	overflow: hidden;
}

.component-container, .list-container {
	width: 100%;
	min-height: 100px;
	margin: 5px auto;
	padding: 1rem 1.5rem;
	overflow: hidden;
}

.list-container {
	overflow-x: hidden;
	overflow-y: scroll;
	max-height: 40vh;	
}

.input {
	width: 100%;
	height: 34px;
	padding: 0.6rem;
	border: 2px solid #ddd;
	border-radius: 3px;
	box-shadow: inset 0px 1px 3px rgba(0,0,0,0.05);
	box-shadow: var(--component-shadow);
	background: #fff;
	font-family: 'Verdana';
	letter-spacing: 1px;
	float: left;
}

.search-option-container {
	margin: 1rem auto 0px auto;
	float: left;
	position: relative;
}


.search-checkbox-label {
	position: relative;
	font-weight: bold;
	font-size: 14px;
	margin-left: 2rem;
	display: inline-block;
}

.todo-container {
	width: 100%;
	min-height: 60px;
	margin-left: 0px;
	text-align: left;
	position: relative;
	overflow: hidden;
	padding: 5px;
}

.todo-description-container {
	position: relative;
	display: inline-block;
	text-align: left;
	top: 6px;
	margin-left: 2rem;
	margin-bottom: 10px;
	font-size: 14px;
	width: 80%;
	float: left;
}

.todo-title {
	display: block;
	color: #555;
	color: var(--text-color);
	font-weight: bold;
}

.todo-date {
	display: block;
	font-size: 12px;
	color: #888;
	color: var(--text-light);
}

.btn-large {
	width: 100%;
	height: 34px;
	border: none;
	border-radius: 2px;
	background: steelblue;
	background: var(--secondary);
	color: #fff;
	margin: 0.7rem auto;
	outline: none;
	opacity: 0.8;
}
.btn-large:hover {
	background: steelblue;
	background: var(--secondary);
	opacity: 1;
}



.check-box {
	position: relative;
	width: 27px;
	height: 17px;
	float: left;
	overflow: hidden;
}

.right {
	width: 60%;
	height: 30%;
	border-right:2px solid white;
	border-top:2px solid white;
	-webkit-transform: rotateZ(120deg);
	        transform: rotateZ(120deg);
	margin: auto;
	position: absolute;
	top: 30%;
	left: 20%;
}

.btn-del {
	width: 25px;
	height: 25px;
	border-radius: 50%;
	border: none;
	float: right;
	margin-top: 5%;
	font-size: 1.2rem;
	color: #fff;
	background: #a77;
	outline: none;
	line-height: 25px;
}

.btn-del:hover {
	background: #d55;
}
